import React, { useEffect, useState } from "react";
// import { useHistory } from "react-router-dom";
import axios from "axios";
import Swal from "sweetalert2";
import liff from "@line/liff";
import config from "../config";
import {
  MDBEdgeHeader,
  MDBFreeBird,
  MDBCol,
  MDBRow,
  MDBCardBody,
  MDBAnimation,
  MDBBtn,
  MDBIcon,
  MDBListGroup,
  MDBListGroupItem,
  MDBBadge,
} from "mdbreact";
import "./HomePage.css";
let today = new Date().getTime();
const jwt = require("jsonwebtoken");
function DateTimeThai(datetime, format) {
  let thmonth = {
    "01": "มกราคม",
    "02": "กุมภาพันธ์",
    "03": "มีนาคม",
    "04": "เมษายน",
    "05": "พฤษภาคม",
    "06": "มิถุนายน",
    "07": "กรกฎาคม",
    "08": "สิงหาคม",
    "09": "กันยายน",
    10: "ตุลาคม",
    11: "พฤศจิกายน",
    12: "ธันวาคม",
  };
  let createdDate = "";
  let date = datetime.split(" ");
  let date_str = date[0].split("-");
  let time_str = date[1].split(":");
  let new_date = `${date_str[2]} ${thmonth[date_str[1]]} ${
    parseInt(date_str[0]) + 543
  }`;
  let new_time = `${time_str[0]}:${time_str[1]} น.`;
  if (format === 1) {
    createdDate = new_date;
  } else if (format === 2) {
    createdDate = new_time;
  } else {
    createdDate = new_date + " " + new_time;
  }
  return createdDate;
}

const HomePage = (props) => {
  // const history = useHistory();
  const [userid, setUserID] = useState("0");
  const [docno, setDocno] = useState("");
  const [startdate, setStartDate] = useState("");
  const [starttime, setStartTime] = useState("");
  const [enddate, setEndDate] = useState("");
  const [endtime, setEndTime] = useState("");
  const [meetingHost, setMeetingHost] = useState("");
  const [meetingRoom, setMeetingRoom] = useState("");
  const [humanAmount, setHumanAmount] = useState("");
  const [humenTotal, setHumanTotal] = useState("");
  const [isMeeting, setIsMeeting] = useState(false);
  const [isRegister, setIsRegister] = useState(false);
  const [isCheckIn, setIsCheckIn] = useState("");
  const [isCheckOut, setIsCheckOut] = useState("");
  const [isTimeMeeting, setIsTimeMeeting] = useState(0);
  const [preLoad, setPreLoad] = useState(true);

  const [pname, setPname] = useState("");
  const [fname, setFname] = useState("");
  const [lname, setLname] = useState("");
  const [idcard, setIDcard] = useState("");
  const [email, setEmail] = useState("");
  const [position, setPosition] = useState("");
  const [department, setDepartment] = useState("");
  const [government, setGovernment] = useState("โรงพยาบาลร้อยเอ็ด");
  const [phone, setPhoneNumber] = useState("");
  const [userMeeting, setUserMeeting] = useState([]);
  const [isShowUser, setIsShowUser] = useState(false);
  const CheckUser = (userId) => {
    const base_url = `${config.main_config.APP_URL}/user`;
    const header = {
      "Content-Type": "application/json",
      Authorization: `Bearer ${config.main_config.TOKEN_APP}`,
    };
    axios
      .post(base_url, { userid: userId }, { headers: header })
      .then((resp) => {
        if (resp.data.data.length > 0) {
          // console.log("LoggedIn");
          localStorage.setItem(
            "user",
            JSON.stringify(resp.data.data[0].data_info)
          );
          setPreLoad(false);
          setIsRegister(true);
        } else {
          // console.log("Invalid Logged");
          setPreLoad(false);
          setIsRegister(false);
          localStorage.removeItem("user");
          localStorage.removeItem("userid");
        }
      })
      .catch((error) => {
        console.log(error);
      });
  };
  const submitHandler = (event) => {
    event.preventDefault();
    event.target.className += " was-validated";
    const formData = {
      userid: userid,
      idcard: idcard,
      pname: pname,
      fname: fname,
      lname: lname,
      email: email,
      position: position,
      department: department,
      government: government,
      phone: phone,
    };
    const base_url = `${config.main_config.APP_URL}/user/register`;
    const header = {
      "Content-Type": "application/json",
      Authorization: `Bearer ${config.main_config.TOKEN_APP}`,
    };
    axios.post(base_url, formData, { headers: header }).then((resp) => {
      if (resp.data.code === 200) {
        localStorage.setItem("user", JSON.stringify(resp.data.data.token));
        Swal.fire({
          title: "ลงทะเบียนเรียบร้อย",
          text: "กรุณารอสักครู่...",
          icon: "success",
          showDenyButton: false,
          showCancelButton: false,
          showCloseButton: false,
          confirmButtonText: `ตกลง`,
        }).then((result) => {
          if (result.isConfirmed) {
            window.location.reload();
          }
        });
      } else if (resp.data.code === 400) {
        Swal.fire({
          title: "แจ้งเตือน",
          text: resp.data.msg,
          icon: "warning",
        });
      } else {
        Swal.fire({
          title: "แจ้งเตือน",
          text: resp.data.error,
          icon: "error",
        });
      }
    });
  };
  const getMeeting = (meetingid) => {
    const docno = meetingid;
    const base_url = `${config.main_config.APP_URL}/getMeetingByDocno/${docno}`;
    const header = {
      "Content-Type": "application/json",
      Authorization: `Bearer ${config.main_config.TOKEN_APP}`,
    };
    axios
      .get(base_url, { headers: header })
      .then((resp) => {
        // console.log(resp.data);
        if (resp.data.code === 200) {
          let startDate = new Date(resp.data.data[0].start_date).getTime();
          let endDate = new Date(resp.data.data[0].end_date).getTime();
          if ((startDate - today) / 1000 > 1800) {
            setIsTimeMeeting(1);
          }
          if (today > endDate) {
            setIsTimeMeeting(2);
          }
          setDocno(resp.data.data[0].docno);
          setStartDate(DateTimeThai(resp.data.data[0].start_date, 1));
          setStartTime(DateTimeThai(resp.data.data[0].start_date, 2));
          setEndDate(DateTimeThai(resp.data.data[0].end_date, 1));
          setEndTime(DateTimeThai(resp.data.data[0].end_date, 2));
          setMeetingHost(resp.data.data[0].meeting_host_name);
          setMeetingRoom(resp.data.data[0].meeting_name);
          setHumanAmount(resp.data.data[0].human_amount);
          setHumanTotal(resp.data.total[0].total_meeting);
          setIsMeeting(true);
          localStorage.setItem("isMeeting", "true");
        } else if (resp.data.code === 400) {
          localStorage.setItem("isMeeting", "false");
          setIsMeeting(false);
        }
      })
      .catch((error) => {
        console.log(error);
      });
  };
  const checkIn = (userid, meetingid) => {
    const base_url = `${config.main_config.APP_URL}/saveCheckIn`;
    const header = {
      "Content-Type": "application/json",
      Authorization: `Bearer ${config.main_config.TOKEN_APP}`,
    };
    axios
      .post(base_url, { userid: userid, docno: meetingid }, { headers: header })
      .then((resp) => {
        if (resp.data.code === 200) {
          Swal.fire({
            title: "เช็คอินเข้าประชุมเรียบร้อย",
            text: `เวลา ${DateTimeThai(
              resp.data.check_date[0].check_in_date,
              3
            )}`,
            showDenyButton: false,
            showCancelButton: false,
            showCloseButton: false,
            confirmButtonText: `ตกลง`,
          }).then((result) => {
            if (result.isConfirmed) {
              window.location.reload();
            }
          });
        } else if (resp.data.code === 400) {
          Swal.fire({
            title: "แจ้งเตือน",
            text: resp.data.msg,
            icon: "error",
          });
        }
      })
      .catch((error) => {
        console.log(error);
      });
  };
  const checkOut = (userid, meetingid) => {
    const base_url = `${config.main_config.APP_URL}/saveCheckOut`;
    const header = {
      "Content-Type": "application/json",
      Authorization: `Bearer ${config.main_config.TOKEN_APP}`,
    };
    axios
      .post(base_url, { userid: userid, docno: meetingid }, { headers: header })
      .then((resp) => {
        if (resp.data.code === 200) {
          Swal.fire({
            title: "เช็คเอ้าท์ออกจากประชุมเรียบร้อย",
            text: `เวลา ${DateTimeThai(
              resp.data.check_date[0].check_out_date,
              3
            )}`,
            showDenyButton: false,
            showCancelButton: false,
            showCloseButton: false,
            confirmButtonText: `ตกลง`,
          }).then((result) => {
            if (result.isConfirmed) {
              window.location.reload();
            }
          });
        } else if (resp.data.code === 400) {
          Swal.fire({
            title: "แจ้งเตือน",
            text: resp.data.msg,
            icon: "error",
          });
        }
      })
      .catch((error) => {
        console.log(error);
      });
  };
  const getCheckInCheckOut = (userID, meetingid) => {
    const base_url = `${config.main_config.APP_URL}/getCheckInCheckOut/${userID}/${meetingid}`;
    const header = {
      "Content-Type": "application/json",
      Authorization: `Bearer ${config.main_config.TOKEN_APP}`,
    };
    axios
      .get(base_url, { headers: header })
      .then((resp) => {
        // console.log(resp.data);
        if (resp.data.code === 200) {
          setIsCheckIn(
            resp.data.data[0].check_in_date !== null
              ? DateTimeThai(resp.data.data[0].check_in_date, 3)
              : ""
          );
          setIsCheckOut(
            resp.data.data[0].check_out_date !== null
              ? DateTimeThai(resp.data.data[0].check_out_date, 3)
              : ""
          );
        }
      })
      .catch((error) => {
        console.log(error);
      });
  };

  const getUserMeetingByMeetingID = (meetingid) => {
    setIsShowUser(true);
    const base_url = `${config.main_config.APP_URL}/getUserMeetingByMeetingID/${meetingid}`;
    const header = {
      "Content-Type": "application/json",
      Authorization: `Bearer ${config.main_config.TOKEN_APP}`,
    };
    axios
      .get(base_url, { headers: header })
      .then((resp) => {
        console.log(resp.data);
        if (resp.data.code === 200) {
          let mapUser = [];
          resp.data.data.map((data) => {
            if (data.data_info !== null) {
              let decoded = jwt.verify(
                data.data_info,
                config.main_config.TOKEN_APP
              );
              let user = {
                check_in_date: data.check_in_date,
                check_out_date: data.check_out_date,
                fullname: `${decoded.pname}${decoded.fname} ${decoded.lname}`,
              };
              mapUser.push(user);
            }
          });
          setUserMeeting(mapUser);
          setIsShowUser(!true);
        }
      })
      .catch((error) => {
        console.log(error);
      });
  };

  const InitailizeLiff = () => {
    // setUserID("U0ce66a9d268b3f1d81d04b30631acc87");
    // CheckUser("U0ce66a9d268b3f1d81d04b30631acc87");
    // let queryString = props.location.search;
    // let str = queryString.split("=");
    // let params = str.length > 1 ? str[1] : 0;
    // getMeeting(params);
    // getCheckInCheckOut("U0ce66a9d268b3f1d81d04b30631acc87", params);
    liff.init(
      {
        liffId: "1655384297-BvEewepx",
      },
      () => {
        if (liff.isLoggedIn()) {
          liff.getProfile().then((profile) => {
            localStorage.setItem("profile-line", JSON.stringify(profile));
            setUserID(profile.userId);
            CheckUser(profile.userId);
            let queryString = props.location.search;
            let str = queryString.split("=");
            let params = str.length > 1 ? str[1] : 0;
            getMeeting(params);
            getCheckInCheckOut(profile.userId, params);
          });
        }
      },
      (err) => console.log(err)
    );
  };

  useEffect(() => {
    InitailizeLiff();
  }, []);
  return (
    <div>
      <div className={preLoad === true ? "loading" : "hidden"}>
        Loading&#8230;
      </div>
      <MDBEdgeHeader
        color="red lighten-5"
        className="sectionPage"
        style={{ height: 100 }}
      />
      <div className="mt-3 mb-5">
        <MDBAnimation type="zoomIn" duration="500ms">
          <MDBFreeBird>
            <MDBRow>
              <MDBCol
                md="10"
                className="mx-auto float-none white z-depth-1 py-2 px-2"
              >
                <MDBCardBody>
                  <div>
                    <MDBCol>
                      <h6 className="h6-responsive mb-4 text-center">
                        <strong className="font-weight-bold text-primary">
                          กรุณา Scan QR Code เพื่อเข้าร่วมประชุม
                        </strong>
                      </h6>
                    </MDBCol>
                  </div>
                  {/* {JSON.stringify(isMeeting)}
                  {JSON.stringify(isRegister)} */}
                  <div
                    className={
                      isMeeting === true && isRegister === true ? "" : "hidden"
                    }
                  >
                    <MDBCol md="12">
                      <blockquote className="blockquote bq-primary">
                        <p className="bq-title" style={{ fontSize: "16px" }}>
                          {meetingRoom}
                        </p>
                        <strong style={{ fontSize: "16px" }}>
                          หัวข้อ {meetingHost}
                        </strong>
                        <div>
                          <strong style={{ fontSize: "14px" }}>
                            {startdate === enddate
                              ? "วันที่ " + startdate
                              : "ระหว่างวันที่ " + startdate + " - " + enddate}
                          </strong>
                        </div>
                        <div>
                          <small style={{ fontSize: "14px" }}>
                            เวลา {starttime} - {endtime}
                          </small>
                        </div>
                        <div>
                          <small style={{ fontSize: "14px" }}>
                            เข้าประชุมแล้ว {humenTotal}/{humanAmount} คน
                          </small>
                        </div>
                      </blockquote>
                    </MDBCol>
                    <MDBCol className="text-center">
                      <div>
                        <span
                          className={
                            isTimeMeeting === 0
                              ? "hidden"
                              : isTimeMeeting === 1
                              ? "text-primary"
                              : "text-danger"
                          }
                        >
                          {isTimeMeeting === 1
                            ? "สามารถเช็คอินเข้าห้องประชุมได้ก่อน 30 นาที"
                            : "จบการประชุมแล้ว"}
                        </span>
                      </div>
                      <div>
                        <span className="text-success">
                          {isCheckIn !== "" ? "เช็คอินเมื่อ " + isCheckIn : ""}
                        </span>
                      </div>
                      <div className="mt-3">
                        <span className="text-danger">
                          {isCheckOut !== ""
                            ? "เช็คเอาท์เมื่อ " + isCheckOut
                            : ""}
                        </span>
                      </div>
                      <div className={isTimeMeeting === 1 ? "hidden" : ""}>
                        <div>
                          <MDBBtn
                            gradient="blue"
                            size="sm"
                            className={isCheckIn !== "" ? "hidden" : ""}
                            onClick={() => checkIn(userid, docno)}
                          >
                            Check-in
                          </MDBBtn>
                          <MDBBtn
                            size="sm"
                            color="danger"
                            className={isCheckOut !== "" ? "hidden" : ""}
                            onClick={() => checkOut(userid, docno)}
                          >
                            Check-out
                          </MDBBtn>
                        </div>
                      </div>
                    </MDBCol>
                    <MDBCol className="text-center">
                      <div>
                        <strong
                          onClick={() => getUserMeetingByMeetingID(docno)}
                          className="btn btn-outline-info"
                        >
                          รายชื่อผู้เข้าร่วม
                        </strong>
                        {isShowUser && (
                          <div className="my-3 d-flex justify-content-around">
                            <div className="spinner-border fast" role="status">
                              <span className="sr-only">Loading...</span>
                            </div>
                          </div>
                        )}
                        <div
                          className={`text-center ${
                            userMeeting === null ? "hidden" : ""
                          }`}
                        >
                          <MDBListGroup>
                            {userMeeting.map((data) => {
                              return (
                                <div key={JSON.stringify(data)}>
                                  <MDBListGroupItem className="align-items-center">
                                    <strong className="mr-3">
                                      {data.fullname}
                                    </strong>
                                    <MDBBadge
                                      color="primary"
                                      pill
                                      className="mr-3"
                                    >
                                      {data.check_in_date !== null
                                        ? "เช็คอินเวลา " +
                                          DateTimeThai(data.check_in_date, 2)
                                        : ""}
                                    </MDBBadge>
                                    {data.check_out_date !== null ? (
                                      <MDBBadge color="danger" pill>
                                        {"เช็คเอาท์เวลา " +
                                          DateTimeThai(data.check_out_date, 2)}
                                      </MDBBadge>
                                    ) : (
                                      "ยังไม่เช็คเอาท์"
                                    )}
                                  </MDBListGroupItem>
                                </div>
                              );
                            })}
                          </MDBListGroup>
                          {/* {JSON.stringify(userMeeting)} */}
                        </div>
                      </div>
                    </MDBCol>
                  </div>
                  <div className={isRegister ? "hidden" : ""}>
                    <MDBCol md="12">
                      <h5 className="text-primary">ลงทะเบียนผู้ใช้งานใหม่</h5>
                      <form
                        className="needs-validation"
                        onSubmit={submitHandler}
                      >
                        <MDBRow>
                          <MDBCol md="4" className="mb-3">
                            <label htmlFor="pname" className="grey-text">
                              คำนำหน้า <span className="text-danger">*</span>
                            </label>
                            <input
                              onChange={(text) => setPname(text.target.value)}
                              type="text"
                              value={pname}
                              className="form-control"
                              name="pname"
                              required
                            />
                          </MDBCol>
                          <MDBCol md="4" className="mb-3">
                            <label htmlFor="fname" className="grey-text">
                              ชื่อ <span className="text-danger">*</span>
                            </label>
                            <input
                              onChange={(text) => setFname(text.target.value)}
                              type="text"
                              value={fname}
                              className="form-control"
                              name="fname"
                              required
                            />
                          </MDBCol>
                          <MDBCol md="4" className="mb-3">
                            <label htmlFor="lname" className="grey-text">
                              นามสกุล <span className="text-danger">*</span>
                            </label>
                            <input
                              onChange={(text) => setLname(text.target.value)}
                              type="text"
                              value={lname}
                              className="form-control"
                              required
                            />
                          </MDBCol>
                        </MDBRow>
                        <MDBRow>
                          <MDBCol md="4" className="mb-3">
                            <label htmlFor="email" className="grey-text">
                              เลขบัตรประชาชน{" "}
                              <span className="text-danger">*</span>
                            </label>
                            <input
                              onChange={(text) => setIDcard(text.target.value)}
                              type="number"
                              value={idcard}
                              className="form-control"
                            />
                          </MDBCol>
                          <MDBCol md="4" className="mb-3">
                            <label htmlFor="email" className="grey-text">
                              อีเมล <span className="text-danger">*</span>
                            </label>
                            <input
                              onChange={(text) => setEmail(text.target.value)}
                              type="email"
                              value={email}
                              className="form-control"
                            />
                            <small
                              id="emailHelp"
                              className="form-text text-muted"
                            >
                              อีเมลที่สามารถติดต่อได้จริง
                            </small>
                          </MDBCol>
                          <MDBCol md="4" className="mb-3">
                            <label htmlFor="position" className="grey-text">
                              ตำแหน่ง <span className="text-danger">*</span>
                            </label>
                            <input
                              onChange={(text) =>
                                setPosition(text.target.value)
                              }
                              type="text"
                              value={position}
                              className="form-control"
                            />
                          </MDBCol>
                        </MDBRow>
                        <MDBRow>
                          <MDBCol md="4" className="mb-3">
                            <label htmlFor="department" className="grey-text">
                              หน่วยงาน <span className="text-danger">*</span>
                            </label>
                            <input
                              onChange={(text) =>
                                setDepartment(text.target.value)
                              }
                              type="text"
                              value={department}
                              className="form-control"
                            />
                          </MDBCol>
                          <MDBCol md="4" className="mb-3">
                            <label htmlFor="main_company" className="grey-text">
                              ส่วนราชการ <span className="text-danger">*</span>
                            </label>
                            <input
                              onChange={(text) =>
                                setGovernment(text.target.value)
                              }
                              type="text"
                              value={government}
                              className="form-control"
                              readOnly
                            />
                          </MDBCol>
                          <MDBCol md="4" className="mb-3">
                            <label htmlFor="phone_number" className="grey-text">
                              เบอร์โทร
                            </label>
                            <input
                              onChange={(text) =>
                                setPhoneNumber(text.target.value)
                              }
                              type="text"
                              value={phone}
                              className="form-control"
                            />
                          </MDBCol>
                        </MDBRow>
                        <MDBBtn gradient="blue" type="submit">
                          บันทึก
                        </MDBBtn>
                      </form>
                    </MDBCol>
                  </div>
                </MDBCardBody>
              </MDBCol>
            </MDBRow>
          </MDBFreeBird>
        </MDBAnimation>
        <div
          className={
            isMeeting && !isRegister
              ? "hidden"
              : isMeeting && isRegister
              ? "hidden"
              : "footer"
          }
        >
          {/* !isMeeting && !isRegister? "hidden": !isMeeting && isRegister? "hidden": "footer" */}
          <MDBCol className="text-center">
            <MDBBtn
              floating="true"
              gradient="blue"
              rounded
              color="danger"
              onClick={() =>
                (window.location = "https://line.me/R/nv/QRCodeReader")
              }
            >
              <MDBIcon icon="qrcode" size="4x" />
              <br />
              <strong className="ml-2" style={{ paddingBottom: "15px" }}>
                SCAN QR Code
              </strong>
            </MDBBtn>
          </MDBCol>
        </div>
      </div>
    </div>
  );
};

export default HomePage;
